import "./newContact.css";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { JsConst } from "../../../constant/JsConst";
import { OptionsSelect } from "../../../components/OptionsSelect";
import { Modal } from "../../../components/Modal";
import { FileUploader } from "../../../components/FileUploader";
import { AddNewCategoryModal } from "../../../components/Modal/AddNewCategoryModal";
import { deleteCategoryAction, deleteCompanyAction, deleteJobTitleAction, deleteTerritoryAction } from "../../../redux/categoryManager/apiCalls";
import { createContact } from "../../../redux/contact/apiCalls";
import { territoryConst } from "../../../constant/Model/territoryConst";


export default function NewContact() {
    const { currentUser } = useSelector(state => state.user)
    const { categories, jobTitles, companies, territories } = useSelector(state => state.categoryManager)

    const [modalProps, setModalProps] = useState(null)
    const [inputs, setInputs] = useState({})
    const [selectedCat, setSelectedCat] = useState(null)

    const dispatch = useDispatch();
    const navigate = useNavigate()

    const handleChange = ({ target }) => {
        let { name, value } = target
        value = handleCat(name, value)
        setInputs((prev) => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (ev) => {
        ev.preventDefault()
        await createContact(dispatch, inputs, currentUser)
        navigate('/admin/contacts')
    }

    const handleCat = (name, value) => {
        if (name === JsConst.PHONE || name === JsConst.LINKEDIN_LINKS || name === JsConst.EMAIL) return value.split(",")
        else if (name === JsConst.CATEGORY) setSelectedCat(value)
        return value
    };

    const uploadContactImage = (value, name) => {
        setInputs((prev) => ({ ...prev, [name]: value }));
    }


    const getCompanyByCategory = (companies) => {
        if (companies && selectedCat) {
            return companies.filter(company => company[JsConst.CATEGORY] === selectedCat.toLowerCase())
        } else return companies
    }

    const onDeleteOption = (name, option) => {
        const res = window.confirm(`Are you sure you want to delete?`)
        if (!res) return
        switch (name) {
            case JsConst.CATEGORY:
                return deleteCategoryAction(dispatch, option._id)
            case JsConst.COMPANY:
                return deleteCompanyAction(dispatch, option._id)
            case JsConst.JOBTITLE:
                return deleteJobTitleAction(dispatch, option._id)
            case JsConst.territory:
                return deleteTerritoryAction(dispatch, option._id)
            default:
                break;
        }
    }

    const getModalProps = () => {
        return {
            setModalProps,
            categories
        }
    }

    return (
        <div className="newContact">
            <h1 className="addContactTitle">New Contact</h1>
            <form className="addContactForm" onSubmit={handleSubmit}>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Categories</label>
                        <span onClick={() => setModalProps({ type: JsConst.CATEGORIES, idx: 2 })}>Add New</span>
                    </div>
                    <OptionsSelect
                        options={categories}
                        cb={handleChange}
                        removeCb={onDeleteOption}
                        accessor={JsConst.TITLE}
                        name={JsConst.CATEGORY} />
                </div>

                <div className="addContactItem">
                    <div className="label-container">
                        <label>Company</label>
                        <span onClick={() => setModalProps({ type: JsConst.COMPANIES, idx: 3 })}>Add New</span>
                    </div>
                    <OptionsSelect
                        options={getCompanyByCategory(companies)}
                        cb={handleChange}
                        removeCb={onDeleteOption}
                        accessor={JsConst.COMPANY}
                        name={JsConst.COMPANY} />
                </div>

                <div className="addContactItem">
                    <label>Name</label>
                    <input name={JsConst.NAME} type="text" placeholder="John" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Family Name</label>
                    <input name={JsConst.FAMILY_NAME} type="text" placeholder="John" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Job Titles</label>
                        <span onClick={() => setModalProps({ type: JsConst.JOBTITLE, idx: 0 })}>Add New</span>
                    </div>
                    <OptionsSelect
                        options={jobTitles}
                        cb={handleChange}
                        removeCb={onDeleteOption}
                        accessor={JsConst.TITLE}
                        name={JsConst.JOBTITLE} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Territories</label>
                        <span onClick={() => setModalProps({ type: JsConst.TERRITORIES, idx: 1 })}>Add New</span>
                    </div>
                    <OptionsSelect
                        options={territories}
                        cb={handleChange}
                        removeCb={onDeleteOption}
                        accessor={territoryConst.TERRITORY_NAME}
                        name={JsConst.territory} />
                </div>
                <div className="addContactItem">
                    <label>Description</label>
                    <input name={JsConst.DESC} type="text" placeholder="description..." onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Email</label>
                    <input name={JsConst.EMAIL} type="text" placeholder="john@gmail.com" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Phone</label>
                    <input name={JsConst.PHONE} type="text" placeholder="+972.." onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Price</label>
                    <input name={JsConst.PRICE} type="number" placeholder="100" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>LinkedIn Links</label>
                    <input name={JsConst.LINKEDIN_LINKS} type="text" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>In Stock</label>
                    <select name={JsConst.IN_STOCK} onChange={handleChange}>
                        <option value="true">Yes</option>
                        <option value="false">No</option>
                    </select>
                </div>
                <div className="addContactItem">
                    <label>Image</label>
                    <FileUploader cb={uploadContactImage} name={JsConst.IMG} />
                </div>
                <div className="add-contact-button--container">
                    <button className="addContactButton">
                        Create
                    </button>
                </div>
            </form>
            {
                modalProps && <Modal {...getModalProps()} >
                    <AddNewCategoryModal {...modalProps} {...getModalProps()} />
                </Modal>
            }
        </div>
    );
}