import { useSelector } from "react-redux";
import { mobile } from "../responsive";
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Announcement from "../components/Announcement";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import { Link, Outlet, useNavigate } from "react-router-dom";

const Container = styled.div``;

const Wrapper = styled.div`
  padding: 20px;
  ${mobile({ padding: "10px" })}
`;

const Title = styled.h1`
  font-weight: 300;
  text-align: center;
`;

const Top = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
`;

const TopButton = styled.button`
  padding: 10px;
  font-weight: 600;
  cursor: pointer;
  border: ${(props) => props.type === "filled" && "none"};
  background-color: ${(props) =>
    props.type === "filled" ? "black" : "transparent"};
  color: ${(props) => props.type === "filled" && "white"};
`;

const TopTexts = styled.div`
  ${mobile({ display: "none" })}
`;
const TopText = styled.span`
  text-decoration: underline;
  cursor: pointer;
  margin: 0px 10px;
`;

const Bottom = styled.div`
  display: flex;
  justify-content: space-between;
  ${mobile({ flexDirection: "column" })}
`;

const Cart = () => {
  const cart = useSelector((state) => state.cart);
  const { currentUser } = useSelector(state => state.user)
  const [cartType, setCartType] = useState('contact')
  const navigate = useNavigate()


  useEffect(() => {
    if (cart.credit.credits.length && !cart.contact.contacts.length) return navigate('/cart/credit')
    else if (cart.contact.contacts.length && !cart.credit.credits.length) return navigate('/cart/contact')
  }, [])

  if (!cart[cartType]) return <Container><Title>Loading...</Title></Container>
  return (
    <Container>
      <Navbar />
      <Announcement />
      <Wrapper>
        <Title>YOUR <span className="text-uppercase">{cartType}</span> BAG</Title>
        <Top>
          <TopButton onClick={() => navigate(-1)}>CONTINUE SHOPPING</TopButton>
          <TopTexts>
            <TopText><Link to="/cart/contact" onClick={() => setCartType('contact')}>Your Contacts ({currentUser ? currentUser.contactTransactions.length : 0})</Link></TopText>
            <TopText><Link to="/cart/credit" onClick={() => setCartType('credit')}>Your Credits Cart ({cart.credit.credits.length})</Link></TopText>
          </TopTexts>
          <TopButton type="filled">CHECKOUT NOW</TopButton>
        </Top>
        <Bottom>
          <Outlet />
        </Bottom>
      </Wrapper>
      <Footer />
    </Container>
  );
};

export default Cart;


