import { JobTitleEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const jobTitleService = {
    query,
    removeJobTitle,
    updateJobTitle,
    createJobTitle,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(JobTitleEP.default)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get job titles')
    } catch (err) {
        throw err
    }
}

async function removeJobTitle(jobTitleId) {
    try {
        const content = { id: jobTitleId }
        return await httpService.post(`${JobTitleEP.default}/${jobTitleId}`, content)
    } catch (err) {
        throw err
    }
}

async function updateJobTitle(jobTitle) {
    try {
        if (jobTitle._id) {
            const content = { jobTitle }
            const res = await httpService.post(`${JobTitleEP.update}/${jobTitle._id}`, content)
            if (res.status !== 'ok') throw new Error("Couldn't save contact")
        }
    } catch (err) {
        throw err
    }
}

async function createJobTitle(jobTitle) {
    try {
        const content = { jobTitle }
        const res = await httpService.post(`${JobTitleEP.default}/create`, content)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create job title")
    } catch (err) {
        throw err
    }
}