import { categoryService } from '../../services/category.service';
import { companyService } from '../../services/company.service';
import { jobTitleService } from '../../services/jobTitle.service';
import { territoryService } from '../../services/territory.service';
import { fetchData, fetchDataError, fetchDataSuccess } from "../app/appRedux";
import {
    setAllCategoriesManager,
    setCategories,
    deleteCategory,
    saveCategory,
    saveCompany,
    deleteCompany,
    deleteJobTitle,
    saveJobTitle,
    saveTerritory,
    deleteTerritory
} from './categoryManagerRedux';


export const getAllCategories = async (dispatch) => {
    try {
        const categories = await categoryService.getAllCategories()
        dispatch(setAllCategoriesManager(categories))
    } catch (err) {
        console.log('Error => ', err)
    }
}

// CATEGORY
export const getCategories = async (dispatch) => {
    try {
        const categories = await categoryService.query()
        dispatch(setCategories(categories));
    } catch (err) {
        console.log('Error => ', err)
    }
}

export const createCategory = async (dispatch, category) => {
    dispatch(fetchData());
    try {
        const savedCategory = await categoryService.createCategory(category)
        dispatch(saveCategory(savedCategory))
        dispatch(fetchDataSuccess())
    } catch (err) {
        console.log('Error => ', err)
        dispatch(fetchDataError());
    }
};

export const deleteCategoryAction = async (dispatch, id) => {
    dispatch(fetchData());
    try {
        await categoryService.removeCategory(id)
        dispatch(deleteCategory(id))
        dispatch(fetchDataSuccess())
    } catch (err) {
        console.log('Error => ', err)
        dispatch(fetchDataError());
    }
};

