import { CategoryEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const categoryService = {
    query,
    removeCategory,
    updateCategory,
    createCategory,
    getAllCategories,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(CategoryEP.default)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get categories')
    } catch (err) {
        throw err
    }
}

async function removeCategory(catId) {
    try {
        const contact = { id: catId }
        return await httpService.post(`${CategoryEP.default}/${catId}`, contact)
    } catch (err) {
        throw err
    }
}

async function updateCategory(category) {
    try {
        if (category._id) {
            const content = { category }
            const res = await httpService.post(`${CategoryEP.update}/${category._id}`, content)
            if (res.status !== 'ok') throw new Error("Couldn't save contact")
        }
    } catch (err) {
        throw err
    }
}

async function createCategory(category) {
    try {
        const content = { category }
        const res = await httpService.post(`${CategoryEP.create}`, content)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create category")
    } catch (err) {
        throw err
    }
}

async function getAllCategories() {
    try {
        const res = await httpService.get(`${CategoryEP.default}/all-categories`)
        if (res.status === 'ok') return res.content
        else throw new Error('Cannot get all categories')
    } catch (err) {
        throw err
    }
}