import { useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { JsConst } from "../../constant/JsConst"
import { createCategory, createCompany, createJobTitle, createTerritory } from "../../redux/categoryManager/apiCalls"
import { utilService } from "../../services/util.service"
import { OptionsSelect } from "../OptionsSelect"

export const AddNewCategoryModal = (porps) => {
    const { type, setModalProps, idx } = porps

    const { categories } = useSelector(state => state.categoryManager)

    const [form, setForm] = useState({})
    const dispatch = useDispatch()

    const updateCategory = async () => {
        try {
            switch (type) {
                case JsConst.CATEGORIES:
                    return createCategory(dispatch, form)
                case JsConst.COMPANIES:
                    return createCompany(dispatch, form)
                case JsConst.JOBTITLE:
                    return createJobTitle(dispatch, form)
                case JsConst.TERRITORIES:
                    return createTerritory(dispatch, form)
                default:
                    break;
            }
        } catch (err) {
            console.log('Error ', err)
        }
    }

    const handleChange = ({ target }) => {
        let { value, name } = target
        if (name === JsConst.CATEGORY) value = value.toLowerCase()
        setForm((prevState) => ({ ...prevState, [name]: value }))
    }

    const onSubmit = async (ev) => {
        ev.preventDefault()
        updateCategory()
        setModalProps(null)
    }

    return (
        <form onSubmit={onSubmit} className="option-modal-container">
            <h2>Add new {type}</h2>
            <div className="option-modal-inputs-container">
                <label>{utilService.getFirstLetterUppercase(type)} Name</label>
                <input type="text" onChange={handleChange} name={JsConst.TITLE} />

            </div>

            {type === JsConst.COMPANIES && <div className="option-modal-inputs-container">
                <label>Select Category</label>
                <OptionsSelect
                    options={categories}
                    cb={handleChange}
                    accessor={JsConst.TITLE}
                    name={JsConst.CATEGORY} />
            </div>}
            <div>
                <button className="btn primary-btn">Save</button>
            </div>
        </form>
    )
}