import { CircularProgress } from '@material-ui/core';
import styled from 'styled-components';

const Button = styled.button`
    width: 100%;
    height: 40px;
    background-color: #008080;
    color: #e9eef8;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    cursor: pointer;
`;

export const LoadingButton = ({ onClick, disabled, isLoading, children }) => {

    const handleClick = () => {
        if (onClick) onClick()
    }

    return (
        <Button
            disabled={disabled || isLoading}
            onClick={handleClick}
        >
            {isLoading ? (
                <CircularProgress size={24} color="inherit" />
            ) : (
                children
            )}
        </Button>
    );
};
