import { createSlice } from "@reduxjs/toolkit";
import { CONTACT_VALUE } from "../../config/appConfig";

const cartSlice = createSlice({
  name: "cart",
  initialState: {
    contact: {
      contacts: [],
      quantity: 0,
      total: 0,
    },
    credit: {
      credits: [],
      quantity: 0,
      total: 0,
    }
  },
  reducers: {
    addContact: (state, action) => {
      state.contact.contacts.push(action.payload);
      state.contact.total += action.payload.price
      state.contact.quantity = state.contact.contacts.length;
    },
    removeContact: (state, action) => {
      state.contact.contacts = state.contact.contacts.filter(contact => action.payload._id !== contact._id)
      state.total -= action.payload.price
      state.contact.quantity = state.contact.contacts.length;
    },
    cleanContactCart: (state, action) => {
      state.contact.contacts = []
      state.contact.quantity = 0
      state.contact.total = 0
    },


    addCredit: (state, action) => {
      state.credit.credits.unshift(action.payload);
      state.credit.total += action.payload.price;
      state.credit.quantity = state.credit.credits.length;
    },
    removeCredit: (state, action) => {
      state.credit.credits = state.credit.credits.splice(action.payload.idx, 1)
      state.credit.total -= action.payload.credit.price;
      state.credit.quantity = state.credit.credits.length;
    },
    cleanCreditCart: (state) => {
      state.credit.credits = []
      state.credit.quantity = 0
      state.credit.total = 0
    }
  },
});

export const {
  addContact,
  removeContact,
  cleanContactCart,
  addCredit,
  removeCredit,
  cleanCreditCart,
} = cartSlice.actions;
export default cartSlice.reducer;
