import { CompanyEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const companyService = {
    query,
    removeCompany,
    updateCompany,
    createCompany,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(CompanyEP.default)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get categories')
    } catch (err) {
        throw err
    }
}

async function removeCompany(companyId) {
    try {
        const content = { id: companyId }
        return await httpService.post(`${CompanyEP.default}/${companyId}`, content)
    } catch (err) {
        throw err
    }
}

async function updateCompany(company) {
    try {
        if (company._id) {
            const content = { company }
            const res = await httpService.post(`${CompanyEP.update}/${company._id}`, content)
            if (res.status !== 'ok') throw new Error("Couldn't save contact")
        }
    } catch (err) {
        throw err
    }
}

async function createCompany(company) {
    try {
        const content = { company }
        const res = await httpService.post(`${CompanyEP.default}/create`, content)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create company")
    } catch (err) {
        throw err
    }
}