import { TerritoryEP } from "../config/appConfig"
import { httpService } from "./http.service"

export const territoryService = {
    query,
    removeTerritory,
    updateTerritory,
    createTerritory,
}

export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(TerritoryEP.default)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get territories')
    } catch (err) {
        throw err
    }
}

async function removeTerritory(territoryId) {
    try {
        const content = { id: territoryId }
        return await httpService.post(`${TerritoryEP.default}/${territoryId}`, content)
    } catch (err) {
        throw err
    }
}

async function updateTerritory(territory) {
    try {
        if (territory._id) {
            const content = { territory }
            const res = await httpService.post(`${TerritoryEP.update}/${territory._id}`, content)
            if (res.status !== 'ok') throw new Error("Couldn't save territory")
        }
    } catch (err) {
        throw err
    }
}

async function createTerritory(territory) {
    try {
        const content = { territory }
        const res = await httpService.post(`${TerritoryEP.default}/create`, content)
        if (res.status === 'ok') return res.content
        else throw new Error("Couldn't create territory")
    } catch (err) {
        throw err
    }
}