import { createSlice } from "@reduxjs/toolkit";

export const creditSlice = createSlice({
    name: "credit",
    initialState: {
        credits: [],
    },
    reducers: {
        //GET ALL
        setCredits: (state, action) => {
            state.credits = action.payload;
        },
    },
});

export const {
    setCredits,
} = creditSlice.actions;

export default creditSlice.reducer;
