import { useSelector } from "react-redux";
import styled from "styled-components";
import { CREDIT_VALUE } from "../config/appConfig";
import { utilService } from "../services/util.service";

const Container = styled.div`
  height: 30px;
  background-color: teal;
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 14px;
  font-weight: 500;
  padding: 0 20px;
  gap: 20px;
`;

const Text = styled.span``

const Announcement = () => {
  const { currentUser } = useSelector(state => state.user)

  if (!currentUser) return null
  else return (
    <Container>
      <Text>Name: {currentUser.fullname}</Text>
      <Text>Credits: {currentUser.credits}</Text>
      <Text>Total Balance: {currentUser.credits * CREDIT_VALUE}$</Text>
      <Text>Income: 0$</Text>
      <Text>Expenses: {utilService.getUserExpensesTotal(currentUser)}$</Text>
    </Container>
  )
};

export default Announcement;
