import { makeStyles } from "@material-ui/core";
import { Delete } from "@mui/icons-material";
import { IconButton, MenuItem, Select } from "@mui/material";
import { useEffect } from "react";
import { useState } from "react";
import { categoryService } from "../services/category.service";

export const OptionsSelect = (props) => {
    const { options, cb, accessor, name, removeCb } = props
    const [selectedOption, setSelectedOption] = useState('');
    const [cat, setCat] = useState(options || [])

    useEffect(() => {
        if (options) setCat(options)
    }, [options])

    const handleSelectChange = (ev) => {
        const { value } = ev.target
        setSelectedOption(value);
        cb(ev)
    };

    const useStyles = makeStyles(() => ({
        menu: {
            maxHeight: 200,
            overflowY: 'auto',
        },
        menuItem: {
            display: 'flex',
            justifyContent: 'space-between',
        },
        deleteButton: {
            marginLeft: 8,
        },
    }));

    const classes = useStyles();

    const onDeleteOption = async (ev, option) => {
        ev.stopPropagation()
        removeCb(name, option)
    }

    return (
        <div className="select-container">
            <Select value={selectedOption} onChange={handleSelectChange}
                MenuProps={{ classes: { paper: classes.menu } }}
                name={name}
            >
                {cat.map((option) => {
                    return <MenuItem key={option._id} value={option[accessor]} className={classes.menuItem}>
                        {option[accessor]}
                        {selectedOption !== option[accessor] && removeCb && <IconButton className={classes.deleteButton} onClick={(ev) => onDeleteOption(ev, option)}>
                            <Delete />
                        </IconButton>}
                    </MenuItem>
                })}
            </Select>
        </div>
    );
}