import { CreditEP } from "../config/appConfig"
import { httpService } from "./http.service"


export const creditService = {
    query,
}

async function query(filterBy = {}) {
    try {
        const res = await httpService.get(CreditEP.default)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get credits')
    } catch (err) {
        throw err
    }
}