import { createSlice } from "@reduxjs/toolkit";

export const categoryManagerSlice = createSlice({
    name: "categoryManager",
    initialState: {
        categories: [],
        companies: [],
        jobTitles: [],
        territories: [],
    },
    reducers: {
        // ALL
        setAllCategoriesManager: (state, action) => {
            state.categories = action.payload.categories
            state.jobTitles = action.payload.jobTitles
            state.companies = action.payload.companies
            state.territories = action.payload.territories
        },

        // CATEGORIES
        setCategories: (state, action) => {
            state.categories = action.payload;
        },
        deleteCategory: (state, action) => {
            state.categories = state.categories.filter((item) => item._id !== action.payload)
        },
        saveCategory: (state, action) => {
            state.categories.push(action.payload)
        },

        // COMPANIES
        setCompanies: (state, action) => {
            state.companies = action.payload;
        },
        deleteCompany: (state, action) => {
            state.companies = state.companies.filter((item) => item._id !== action.payload)
        },
        saveCompany: (state, action) => {
            state.companies.push(action.payload)
        },

        // JOB TITLES
        setJobTitles: (state, action) => {
            state.jobTitles = action.payload;
        },
        deleteJobTitle: (state, action) => {
            state.jobTitles = state.jobTitles.filter((item) => item._id !== action.payload)
        },
        saveJobTitle: (state, action) => {
            state.jobTitles.push(action.payload)
        },

        // TERRITORIES
        setTerritories: (state, action) => {
            state.territories = action.payload;
        },
        deleteTerritory: (state, action) => {
            state.territories = state.territories.filter((item) => item._id !== action.payload)
        },
        saveTerritory: (state, action) => {
            state.territories.push(action.payload)
        },
    },
});

export const {
    setAllCategoriesManager,
    setCategories,
    deleteCategory,
    saveCategory,
    setCompanies,
    deleteCompany,
    saveCompany,
    setJobTitles,
    deleteJobTitle,
    saveJobTitle,
    setTerritories,
    deleteTerritory,
    saveTerritory,
} = categoryManagerSlice.actions;

export default categoryManagerSlice.reducer;
