import "./userList.css";
import { DataGrid } from "@mui/x-data-grid";
import { DeleteOutline } from "@mui/icons-material";
import { Link } from "react-router-dom";
import { useEffect } from "react";
import { getUsers, removeUser } from "../../../redux/user/apiCalls";
import { useDispatch, useSelector } from "react-redux";
import { DUMMY_USER_URL } from "../../../config/appConfig";

export default function UserList() {
  const { users, currentUser } = useSelector((state) => state.user);
  const dispatch = useDispatch()

  useEffect(() => {
    if (!users.length) getUsers(dispatch);
  }, [currentUser]);

  const handleDelete = (id) => {
    const res = window.confirm('Are you sure?')
    if (res) removeUser(dispatch, id, currentUser)
  };

  const columns = [
    { field: "_id", headerName: "ID", width: 90 },
    {
      field: "user",
      headerName: "User",
      width: 200,
      renderCell: (params) => {
        return (
          <div className="userListUser">
            <img className="userListImg" src={params.row?.imgUrl?.url || DUMMY_USER_URL} alt="user" />
            {params.row.username}
          </div>
        );
      },
    },
    { field: "email", headerName: "Email", width: 200 },
    {
      field: "isAdmin",
      headerName: "Admin Status",
      width: 120,
    },
    {
      field: "transaction",
      headerName: "Transaction Volume",
      width: 160,
    },
    {
      field: "action",
      headerName: "Action",
      width: 150,
      renderCell: (params) => {
        return (
          <>
            <Link to={"/admin/user/" + params.row._id}>
              <button className="userListEdit">Edit</button>
            </Link>
            <DeleteOutline
              titleAccess="Remove"
              className="userListDelete"
              onClick={() => handleDelete(params.row._id)}
            />
          </>
        );
      },
    },
  ];

  function getRowId(row) {
    return row._id
  }

  if (!users.length) return <div className="flex-4">Loading...</div>
  return (
    <div className="userList">
      <DataGrid
        rows={users}
        disableSelectionOnClick
        columns={columns}
        pageSize={10}
        checkboxSelection
        getRowId={getRowId}
      />
      <div className="table-create-btn">
        <Link to="/admin/newUser">
          <button className="userAddButton">Create</button>
        </Link>
      </div>
    </div>
  );
}
