import styled from 'styled-components';
import { LoadingButton } from './Core/LoadingButton';
import { useState } from 'react';
import { showSuccessMsg } from '../services/eventBusService';
import { useDispatch } from 'react-redux';
import { addCreditPackage } from '../redux/cart/apiCalls';

const Container = styled.div`
    display: flex;
    background-color: #f5f6f8;
    padding: 10px;
    flex-direction: column;
    min-width: 250px;
    border-radius: 5px;
    transition: all 0.5s ease;
    border: 1px solid #e1e4e5;
    &:hover {
        box-shadow: -1px 10px 20px -15px rgba(0,0,0,0.75);
    }

    ${props => props.theme.breakpoints.medium} {
        width: 100%
    }
`;

const Header = styled.div`
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
`

const Footer = styled.div`
    flex: 1;
    padding-top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`

const ContentDetailsList = styled.div`
    margin: 10px 0;
    display: flex;
    flex-direction: column;
    gap: 5px;
`
const ContentDetailPreview = styled.div`
    padding 5px 5px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e1e4e5;
`
const Text = styled.div`
`

const UppercaseText = styled.span`
    text-transform: uppercase;
`

const Description = styled.div`
    margin-bottom: 10px;
`

export const Credit = ({ credit }) => {
    const [isLoading, setIsLoading] = useState(false)
    const dispatch = useDispatch()

    const onPurchase = async () => {
        try {
            setIsLoading(true)
            showSuccessMsg('Credit just added to the cart!')
            addCreditPackage(dispatch, credit)
        } catch (err) {
            setIsLoading(false)
        } finally {
            setIsLoading(false)
        }
    }

    return (
        <Container>
            <Header>
                <UppercaseText>{credit.name}</UppercaseText>
                <Text>{credit.price} $</Text>
            </Header>

            <ContentDetailsList>
                <ContentDetailPreview>
                    <Text>
                        Credits
                    </Text>
                    <Text>
                        {credit.quantity}
                    </Text>
                </ContentDetailPreview>
            </ContentDetailsList>

            <Footer>
                <Description>{credit.description}</Description>
                <LoadingButton isLoading={isLoading} onClick={() => onPurchase(credit)}>Buy credits</LoadingButton>
            </Footer>
        </Container>
    )
}