import { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import { constLang } from "../constant/language";
import { signup } from "../redux/user/apiCalls";
import { mobile } from "../responsive";
import { LoadingButton } from "../components/Core/LoadingButton";

import RemoveRedEyeOutlinedIcon from '@mui/icons-material/RemoveRedEyeOutlined';
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined';
import { DivCenterItem } from "../constant/style";

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url("https://images.pexels.com/photos/6984661/pexels-photo-6984661.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940")
      center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Wrapper = styled.div`
  width: 40%;
  padding: 20px;
  background-color: white;
  ${mobile({ width: "75%" })}
`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: 300;
`;

const Form = styled.form`
  display: flex;
  flex-wrap: wrap;
`;

const Input = styled.input`
  flex: 1;
  min-width: 40%;
  margin: 20px 10px 0px 0px;
  padding: 10px;
`;

const Agreement = styled.span`
  font-size: 12px;
  margin: 20px 0px;
`;

const EyeButton = styled.div`
  margin-top: 10px
`

const Register = () => {
  const [isPasswordShown, setIsPasswordShown] = useState(false)
  const [form, setForm] = useState({})
  const [isLoading, setIsLoading] = useState(false)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const handleChange = ({ target }) => {
    const { name, value } = target
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const onSubmit = (ev) => {
    ev.preventDefault()
    validateForm()
  }

  const validateForm = async () => {
    setIsLoading(true)
    if (!form.hasOwnProperty(constLang.NAME)
      || !form.hasOwnProperty(constLang.LAST_NAME)
      || !form.hasOwnProperty(constLang.USERNAME)
      || !form.hasOwnProperty(constLang.EMAIL)
      || !form.hasOwnProperty(constLang.PASSWORD)) return alert('Please fill all the fields')

    else if (form[constLang.CONFIRM_PASSWORD] !== form[constLang.PASSWORD]) alert('Passwords do not match')
    else {
      try {
        const user = await signup(dispatch, form)
        if (user) navigate('/')
      } catch (err) {
        console.log('Error in registeration')
      }
    }
    setIsLoading(false)
  }

  return (
    <Container>
      <Wrapper>
        <Title>CREATE AN ACCOUNT</Title>
        <Form onSubmit={onSubmit}>
          <Input type="text" placeholder="name" name={constLang.NAME} onChange={handleChange} />
          <Input type="text" placeholder="last name" name={constLang.LAST_NAME} onChange={handleChange} />
          <Input type="text" placeholder="username" name={constLang.USERNAME} onChange={handleChange} />
          <Input type="email" placeholder="email" name={constLang.EMAIL} onChange={handleChange} />
          <Input type={isPasswordShown ? "text" : "password"} placeholder="password" name={constLang.PASSWORD} onChange={handleChange} />
          <Input type={isPasswordShown ? "text" : "password"} placeholder="confirm password" name={constLang.CONFIRM_PASSWORD} onChange={handleChange} />
          <EyeButton className="flex align-center justify-center" onClick={() => setIsPasswordShown(!isPasswordShown)}>{isPasswordShown ? <VisibilityOffOutlinedIcon /> : <RemoveRedEyeOutlinedIcon />}</EyeButton>
          <Agreement>
            By creating an account, I consent to the processing of my personal
            data in accordance with the <b>PRIVACY POLICY</b>
          </Agreement>
          <LoadingButton isLoading={isLoading}>
            CREATE
          </LoadingButton>
        </Form>
      </Wrapper>
    </Container>
  );
};

export default Register;
