import { CREDIT_VALUE } from "../../config/appConfig"
import { JsConst } from "../../constant/JsConst"


export class ContactTransaction {
    userId
    type
    contactId
    name
    familyName
    jobTitle
    price
    category
    company
    mobile
    phone
    inStock
    img
    contactPriceInCredit
    createdAt = new Date()

    constructor({ contact, type, user }) {
        this.userId = user[JsConst._ID]
        this.type = type
        this.contactId = contact._id
        this.name = contact.name
        this.familyName = contact.familyName
        this.jobTitle = contact.jobTitle
        this.price = contact.price
        this.contactPriceInCredit = contact.price / CREDIT_VALUE
        this.category = contact.category
        this.company = contact.company
        this.mobile = contact.mobile
        this.phone = contact.phone
        this.inStock = contact.inStock
        this.img = contact.img
    }
}