import { constUser } from "./constUser"


export class User {
    username
    email
    password
    isAdmin
    createdAt = new Date()
    updatedAt = null
    imgUrl
    phone
    address
    fullname
    active
    title
    favorites
    credits
    creditTransactions
    contactTransactions

    constructor(user) {
        this.username = user[constUser.USERNAME]
        this.email = user[constUser.EMAIL]
        this.password = user[constUser.PASSWORD]
        this.isAdmin = user[constUser.IS_ADMIN]
        this.createdAt = user[constUser.CREATED_AT]
        this.updatedAt = user[constUser.UPDATED_AT]
        this.imgUrl = user[constUser.IMG_URL]
        this.phone = user[constUser.PHONE]
        this.address = user[constUser.ADDRESS]
        this.fullname = this.setFullname(user)
        this.active = user[constUser.ACTIVE]
        this.title = user[constUser.TITLE]
        this.favorites = user[constUser.FAVORITES]
        this.credits = user[constUser.CREDITS]
        this.creditTransactions = user[constUser.CREDIT_TRANSACTIONS]
        this.contactTransactions = user[constUser.CONTACT_TRANSACTIONS]
    }

    setFullname(user) {
        return `${user.name} ${user.lastName}`
    }
}