import { DataGrid } from "@mui/x-data-grid";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { getUsers, updateUser } from "../../../redux/user/apiCalls";
import { useDispatch, useSelector } from "react-redux";
import { CREDIT_VALUE, DUMMY_USER_URL } from "../../../config/appConfig";
import { constUser } from "../../../model/user/constUser";
import { constTranslate } from "../../../constant/translate";
import { JsConst } from "../../../constant/JsConst";
import { utilService } from "../../../services/util.service";
import { Modal } from "../../../components/Modal";
import { LoadingButton } from "../../../components/Core/LoadingButton";

export const UserBilling = () => {
    const { users, currentUser } = useSelector((state) => state.user);
    const [modalProps, setModalProps] = useState(null)
    const [isLoading, setIsLoading] = useState(false)
    const dispatch = useDispatch()

    useEffect(() => {
        if (!users.length) getUsers(dispatch);
    }, [currentUser]);


    const onEditCredits = async (form) => {
        const res = window.confirm('Are you sure?')
        setIsLoading(true)
        const updatedUser = { ...form, credits: Number(form.credits) }
        if (res) {
            await updateUser(dispatch, updatedUser)
        }
        setIsLoading(false)
        setModalProps(null)
    };

    const columns = [
        {
            field: constUser.FULLNAME,
            headerName: constTranslate.FULLNAME,
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="userListUser">
                        <img className="userListImg" src={params.row?.imgUrl?.url || DUMMY_USER_URL} alt="user" />
                        {params.row.fullname}
                    </div>
                );
            },
        },
        { field: JsConst._ID, headerName: constTranslate.ID, width: 150 },
        { field: constUser.ADDRESS, headerName: constTranslate.ADDRESS, width: 150 },
        { field: constUser.CREDITS, headerName: constTranslate.CREDITS, width: 150 },
        {
            field: 'totalBalance',
            headerName: constTranslate.TOTAL_BALANCE,
            width: 200,
            renderCell: (params) => {

                return (
                    <div className="userListUser">
                        {Number.isNaN(params.row.credits * CREDIT_VALUE) ? 0 : params.row.credits * CREDIT_VALUE}$
                    </div>
                );
            },
        },
        {
            field: JsConst.EXPENSES,
            headerName: constTranslate.EXPENSES,
            width: 150,
            renderCell: (params) => {
                return (
                    <div className="userListUser">
                        {utilService.getUserExpensesTotal(params.row)}
                    </div>
                );
            },
        },
        {
            field: JsConst.INCOME,
            headerName: constTranslate.INCOME,
            width: 150,
            renderCell: () => {
                return (
                    <div className="userListUser">
                        0
                    </div>
                );
            },
        },
        {
            field: constUser.CREDIT_TRANSACTIONS,
            headerName: constTranslate.DATE_OF_LAST_TRANSACTION,
            width: 200,
            renderCell: (params) => {

                const getLastTransactionDate = () => {
                    return utilService.formatMostRecentDate(params.row.creditTransactions)
                }
                return (
                    <div className="userListUser">
                        {getLastTransactionDate()}
                    </div>
                );
            },
        },
        {
            field: "action",
            headerName: "Action",
            width: 150,
            renderCell: (params) => {
                return <button
                    className="userListEdit"
                    onClick={() => setModalProps({ user: params.row })}>
                    Edit
                </button>
            },
        },
    ];

    function getRowId(row) {
        return row._id
    }

    if (!users.length) return <div className="flex-4">Loading...</div>
    return (
        <div className="flex-4">
            <DataGrid
                rows={users}
                disableSelectionOnClick
                columns={columns}
                pageSize={10}
                checkboxSelection
                getRowId={getRowId}
            />
            {
                modalProps && <Modal setModalProps={setModalProps} >
                    <FormModal
                        modalProps={modalProps}
                        name={constUser.CREDITS}
                        title={'Edit User Credits'}
                        inputType={'number'}
                        isLoading={isLoading}
                        onSubmit={onEditCredits} />
                </Modal>
            }
        </div>
    )
}

const FormModal = (props) => {
    const { modalProps, name, title, inputType, isLoading, onSubmit } = props
    const [form, setForm] = useState(modalProps.user)

    const handleChange = ({ target }) => {
        const { value, name } = target
        setForm((prevState) => ({ ...prevState, [name]: value }))
    }

    const handleSubmit = (ev) => {
        ev.preventDefault()
        onSubmit(form)
    }

    return (
        <form onSubmit={handleSubmit}>
            <div className="modal-header">
                <h3>{title}</h3>
            </div>

            <div className="input-wrapper">
                <label htmlFor="credit">Credits</label>
                <input id="credit" type={inputType} name={name} value={form.credits} onChange={handleChange} />
            </div>

            <div style={{ width: '50%', marginTop: '20px' }}>
                <LoadingButton isLoading={isLoading}>
                    {constTranslate.SAVE}
                </LoadingButton>
            </div>
        </form>
    )
}