import { useEffect, useRef, useState } from "react"
import { eventBusService } from "../../services/eventBusService"
import styled, { keyframes } from "styled-components";
import CloseOutlinedIcon from '@mui/icons-material/CloseOutlined';

const fadeIn = keyframes`
from {
    opacity: 0;
    transform: translate(-50%, -10px);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0);
  }
`;

const PopupMessage = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translate(-50%);
  background-color: ${props => props.type === 'success' ? '#008080' : '#e85e6c'};
  border: 1px solid ${props => props.type === 'success' ? '#C3E6CB' : '#F5C6CB'};
  border-radius: 5px;
  padding: 10px;
  color: #ffffff;
  min-width: 100px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  animation: ${fadeIn} 0.2s ease-out;
  z-index: 5;
`;

const CloseButton = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  font-size: 13px;
  color: #ffffff;
  cursor: pointer;
`;

export const UserMsg = () => {
    const [msg, setMsg] = useState(null)
    const removeEvent = useRef()
    const timeoutId = useRef()

    useEffect(() => {
        removeEvent.current = eventBusService.on('show-user-msg', (msg) => {
            setMsg(msg)
            timeoutId.current = setTimeout(() => setMsg(null), 2500)
        })
        return () => {
            removeEvent.current()
            clearTimeout(timeoutId.current)
        }
    }, [])

    const onClose = () => {
        clearTimeout(timeoutId.current)
        setMsg(null)
    }

    if (!msg) return null
    else return (
        <>
            <PopupMessage type={msg.type}>
                {msg.txt}
                <CloseButton onClick={onClose}><CloseOutlinedIcon /></CloseButton>
            </PopupMessage>
        </>
    )
}