import { JsConst } from "../../constant/JsConst"


export class CreditTransaction {
    userId
    type
    creditId
    creditName
    creditQuantity
    packagePrice
    createdAt = new Date()

    constructor({ credit, type, user }) {
        this.userId = user[JsConst._ID]
        this.type = type
        this.creditId = credit[JsConst._ID]
        this.creditName = credit.name
        this.creditQuantity = credit.quantity
        this.packagePrice = credit.price
    }
}